@mixin fontBold {
	font-family: "Montserrat", sans-serif;
	font-weight: 700;
}
@mixin fontRegular {
	font-family: "Montserrat", sans-serif;
	font-weight: 400;
}
@mixin font($font_name, $file_name, $weight, $style) {
	@font-face {
		font-family: $font_name;
		font-display: swap;
		src: url("../fonts/#{$file_name}.woff") format("woff"), url("../fonts/#{$file_name}.woff2") format("woff2");
		font-weight: #{$weight};
		font-style: #{$style};
	}
}
